import * as React from 'react';
import { Tabs, Tab, Typography, Box, useTheme } from '@mui/material';
import {
  Person,
  Work,
  Experience,
  Protfolio,
  SelfEvaluation,
  Education,
} from './modules';

export default function Rusume() {
  const theme = useTheme();
  const [ctrl, setCtrl] = React.useState({
    tab: 0,
    type: 'click',
  });

  const titleStyle = {
    padding: theme.spacing(1, 2),
    background: theme.palette.background.paper,
  };
  const sectionStyle = {
    // minHeight: 300
    '& + $section': {
      marginTop: theme.spacing(2),
    },
  };

  React.useEffect(() => {
    const dom = document.getElementById('section' + ctrl.tab);
    if (dom && ctrl.type === 'click') {
      dom.scrollIntoView({ behavior: 'smooth' });
    }
  }, [ctrl]);

  return (
    <Box
      sx={{
        display: 'flex',
        height: '100vh',
      }}
    >
      <Box
        sx={{
          minWidth: 275,
          backgroundColor: theme.palette.background.paper,
          alignSelf: 'flex-start',
          marginRight: theme.spacing(2),
        }}
      >
        <Person />
      </Box>
      <Box
        id="sections"
        sx={{
          flexGrow: 1,
          maxWidth: 900,
          minWidth: 600,
          overflow: '-moz-scrollbars-none',
          overflowY: 'auto',
          '-ms-overflow-style': 'none',
          '&::-webkit-scrollbar': {
            width: '0 !important',
          },
        }}
      >
        <Box id={'section' + 0} data-tab={0} sx={sectionStyle}>
          <Typography sx={titleStyle} variant="h6">
            工作经历
          </Typography>
          <Work />
        </Box>
        <Box id={'section' + 1} data-tab={1} sx={sectionStyle}>
          <Typography sx={titleStyle} variant="h6">
            项目经历
          </Typography>
          <Experience />
        </Box>
        <Box id={'section' + 2} data-tab={2} sx={sectionStyle}>
          <Typography sx={titleStyle} variant="h6">
            作品集
          </Typography>
          <Protfolio />
        </Box>
        <Box id={'section' + 3} data-tab={3} sx={sectionStyle}>
          <Typography sx={titleStyle} variant="h6">
            自我描述
          </Typography>
          <SelfEvaluation />
        </Box>
        <Box id={'section' + 4} data-tab={4} sx={sectionStyle}>
          <Typography sx={titleStyle} variant="h6">
            教育经历
          </Typography>
          <Education />
        </Box>
      </Box>
      <Box
        sx={{
          minWidth: 275,
          backgroundColor: theme.palette.background.paper,
          alignSelf: 'flex-start',
          marginLeft: theme.spacing(2),
        }}
      >
        <Tabs
          orientation="vertical"
          value={ctrl.tab}
          onChange={(e, val) => setCtrl({ tab: val, type: 'click' })}
        >
          <Tab label="工作经历" />
          <Tab label="项目经历" />
          <Tab label="作品集" />
          <Tab label="自我描述" />
          <Tab label="教育经历" />
        </Tabs>
      </Box>
    </Box>
  );
}
